import React from "react";

function SignInForm({ onLogin, error }) {
  const [state, setState] = React.useState({
    correo: "",
    password: ""
  });

  const handleChange = (evt) => {
    const value = evt.target.value;
    setState({
      ...state,
      [evt.target.name]: value
    });
  };

  const handleOnSubmit = async (evt) => {
    evt.preventDefault();

    const { correo, password } = state;

    try {
      await onLogin(correo, password); // Llamar a la funcin onLogin que viene de App.js
      setState({ correo: "", password: "" }); // Limpiar el formulario
    } catch (error) {
      console.error("Error de inicio de sesin:", error);
    }
  };

  return (
    <div className="form-container sign-in-container">
      <form onSubmit={handleOnSubmit}>
        <h1>Iniciar sesin</h1>
        <input
          type="email"
          placeholder="correo"
          name="correo"
          value={state.correo}
          onChange={handleChange}
          required
        />
        <input
          type="password"
          name="password"
          placeholder="Contrasea"
          value={state.password}
          onChange={handleChange}
          required
        />
        <a href="#">Olvidaste tu contrasea?</a>
        <button type="submit">Iniciar sesin</button>
        {error && <p className="error">{error}</p>} {/* Mostrar errores */}
      </form>
    </div>
  );
}

export default SignInForm;
